<template>
    <div id="container" style="width: 100%; height: 745px;"></div>
</template>

<script>
export default{
name: 'Map',
methods:{
    loadAMap(){
        const script = document.createElement("script");
        script.src = "https://webapi.amap.com/loader.js";
        script.onload = () => {
            this.initAMap();
        };
        document.head.appendChild(script);
    },
    initAMap() {
        window._AMapSecurityConfig = {
            securityJsCode: "1cb204847dd3fb2f9bd9e9592f58fdb1",
        };

        window.AMapLoader.load({
            key: "1cb204847dd3fb2f9bd9e9592f58fdb1",
            version: "2.0",
            })
            .then((AMap) => {
                const map = new AMap.Map("container",{
                    mapStyle: "amap://styles/fresh",
                    viewMode: "2D",                 
                });
                
                const circleMarker = new AMap.CircleMarker({
                    center: [116.326936,40.003213], //  [, ]
                    radius: 10, // 
                    strokeColor: "#F33", // 
                    strokeOpacity: 1, // 
                    strokeWeight: 0, // 
                    fillColor: "#0000ff", // 
                    fillOpacity: 0.5, // 
                    zIndex: 10, // 
                });

                // 
                circleMarker.setMap(map);
            })
            .catch((e) => {
                console.error(e);
            });

    },
},
mounted(){
    this.loadAMap();
}
}
</script>

